<template>
  <div class="distributed-generator">
    <el-card class="main-card" shadow="hover">
      <template #header>
        <div class="card-header">
          <span>分布生成测试用例</span>
          <el-icon><Share /></el-icon>
        </div>
      </template>

      <!-- 功能说明 -->
      <div class="feature-description">
        <el-alert
          title="分布生成功能"
          type="info"
          description="将大型需求文档拆分成多个小模块，分别生成测试用例，最后合并成完整的测试用例集。适用于复杂系统的测试用例生成。"
          show-icon
          :closable="false"
        />
      </div>

      <!-- API选择区域 -->
      <div class="api-selection">
        <h3>选择AI服务</h3>
        <el-radio-group v-model="selectedApi" class="api-radio-group">
          <el-radio-button label="cybotstar">Cybotstar AI</el-radio-button>
          <el-radio-button label="deepseek">DeepSeek AI</el-radio-button>
        </el-radio-group>
        <div class="api-description">
          <span v-if="selectedApi === 'cybotstar'" class="api-desc">
            <el-icon><Star /></el-icon>
            Cybotstar AI - 专业的测试用例生成服务
          </span>
          <span v-if="selectedApi === 'deepseek'" class="api-desc">
            <el-icon><Cpu /></el-icon>
            DeepSeek AI - 强大的代码理解和生成能力
          </span>
        </div>
      </div>

      <!-- 输入方式选择 -->
      <div class="input-mode-selection">
        <h3>选择输入方式</h3>
        <el-radio-group v-model="inputMode" class="mode-radio-group">
          <el-radio-button label="text">文本输入</el-radio-button>
          <el-radio-button label="file">文件上传</el-radio-button>
        </el-radio-group>
      </div>

      <!-- 输入区域 -->
      <div class="input-section">
        <!-- 文本输入 -->
        <div v-if="inputMode === 'text'" class="text-input-area">
          <div class="input-header">
            <h3>需求文档内容</h3>
            <p class="input-tip">请输入完整的需求文档，系统将自动拆分成多个模块进行分布生成</p>
          </div>
          <el-input
            v-model="textInput"
            type="textarea"
            :rows="12"
            placeholder="请输入完整的需求文档内容...\n\n系统将自动识别并拆分成以下模块：\n- 用户管理模块\n- 权限管理模块\n- 数据管理模块\n- 系统配置模块\n等等..."
            class="text-input"
          />
        </div>

        <!-- 文件上传 -->
        <div v-if="inputMode === 'file'" class="file-upload-area">
          <div class="input-header">
            <h3>文件上传</h3>
            <p class="input-tip">上传需求文档，支持多种格式，系统将自动解析并分布生成</p>
          </div>
          <el-upload
            ref="uploadRef"
            class="upload-demo"
            drag
            :auto-upload="false"
            :on-change="handleFileChange"
            :on-remove="handleFileRemove"
            :file-list="fileList"
            accept=".txt,.md,.docx,.doc,.pdf,.xlsx,.xls,.json,.xml,.csv"
            :limit="1"
          >
            <el-icon class="el-icon--upload"><upload-filled /></el-icon>
            <div class="el-upload__text">
              将文件拖到此处，或<em>点击上传</em>
            </div>
            <template #tip>
              <div class="el-upload__tip">
                支持格式：.txt, .md, .docx, .doc, .pdf, .xlsx, .xls, .json, .xml, .csv（最大10MB）
              </div>
            </template>
          </el-upload>
        </div>
      </div>

      <!-- 分布配置 -->
      <div class="distribution-config">
        <h3>分布配置</h3>
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="拆分策略">
              <el-select v-model="splitStrategy" placeholder="请选择拆分策略">
                <el-option label="按功能模块拆分" value="module" />
                <el-option label="按页面拆分" value="page" />
                <el-option label="按业务流程拆分" value="process" />
                <el-option label="智能自动拆分" value="auto" />
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="并发数量">
              <el-input-number v-model="concurrency" :min="1" :max="10" />
            </el-form-item>
          </el-col>
        </el-row>
      </div>

      <!-- 生成按钮 -->
      <div class="action-section">
        <el-button
          type="primary"
          size="large"
          :loading="loading"
          @click="generateDistributedTestCases"
          :disabled="!canGenerate"
          class="generate-btn"
        >
          <el-icon v-if="!loading"><Share /></el-icon>
          {{ loading ? '分布生成中...' : '开始分布生成测试用例' }}
        </el-button>
      </div>
    </el-card>

    <!-- 进度展示 -->
    <el-card v-if="showProgress" class="progress-card" shadow="hover">
      <template #header>
        <div class="card-header">
          <span>生成进度</span>
          <el-icon><Timer /></el-icon>
        </div>
      </template>
      
      <div class="progress-content">
        <el-progress :percentage="overallProgress" :status="progressStatus" />
        <div class="progress-details">
          <p>总体进度: {{ completedModules }}/{{ totalModules }} 个模块已完成</p>
          <div class="module-list">
            <el-tag
              v-for="(module, index) in modules"
              :key="index"
              :type="getModuleTagType(module.status)"
              class="module-tag"
            >
              {{ module.name }} - {{ module.status }}
            </el-tag>
          </div>
        </div>
      </div>
    </el-card>

    <!-- 结果展示区域 -->
    <el-card v-if="result" class="result-card" shadow="hover">
      <template #header>
        <div class="card-header">
          <span>分布生成结果</span>
          <div class="header-actions">
            <el-button
              type="success"
              size="small"
              @click="downloadResult"
              :disabled="!downloadUrl"
            >
              <el-icon><Download /></el-icon>
              下载合并文件
            </el-button>
            <el-button
              size="small"
              @click="copyToClipboard"
            >
              <el-icon><DocumentCopy /></el-icon>
              复制内容
            </el-button>
          </div>
        </div>
      </template>

      <div class="result-content">
        <el-tabs v-model="activeTab" type="border-card">
          <el-tab-pane label="合并结果" name="merged">
            <pre>{{ result }}</pre>
          </el-tab-pane>
          <el-tab-pane 
            v-for="(moduleResult, index) in moduleResults" 
            :key="index"
            :label="moduleResult.name" 
            :name="`module-${index}`"
          >
            <pre>{{ moduleResult.content }}</pre>
          </el-tab-pane>
        </el-tabs>
      </div>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios'
import { ElMessage } from 'element-plus'
import { UploadFilled } from '@element-plus/icons-vue'

export default {
  name: 'DistributedGenerator',
  components: {
    UploadFilled
  },
  data() {
    return {
      textInput: '',
      loading: false,
      result: '',
      downloadUrl: '',
      selectedApi: 'cybotstar',
      inputMode: 'text',
      fileList: [],
      uploadedFile: null,
      splitStrategy: 'auto',
      concurrency: 3,
      showProgress: false,
      overallProgress: 0,
      progressStatus: '',
      completedModules: 0,
      totalModules: 0,
      modules: [],
      moduleResults: [],
      activeTab: 'merged'
    }
  },
  computed: {
    canGenerate() {
      if (this.inputMode === 'text') {
        return this.textInput.trim().length > 0 && this.selectedApi
      } else {
        return this.uploadedFile && this.selectedApi
      }
    }
  },
  methods: {
    async generateDistributedTestCases() {
      if (!this.canGenerate) return
      
      this.loading = true
      this.result = ''
      this.downloadUrl = ''
      this.showProgress = true
      this.overallProgress = 0
      this.progressStatus = 'active'
      this.moduleResults = []
      this.completedModules = 0
      this.totalModules = 0
      this.modules = []

      try {
        const formData = new FormData()
        
        // 添加基本参数
        formData.append('apiProvider', this.selectedApi)
        formData.append('splitStrategy', this.splitStrategy)
        formData.append('concurrency', this.concurrency)
        
        // 根据输入模式添加内容
        if (this.inputMode === 'file' && this.uploadedFile) {
          formData.append('file', this.uploadedFile)
        } else {
          formData.append('content', this.textInput)
        }
        
        // 模拟进度更新
        const progressInterval = setInterval(() => {
          if (this.overallProgress < 90) {
            this.overallProgress += Math.random() * 10
          }
        }, 500)
        
        const response = await axios.post('/api/generate-distributed', formData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        })
        
        clearInterval(progressInterval)
        this.overallProgress = 100

        if (response.data.success) {
          this.result = response.data.content
          this.downloadUrl = response.data.downloadUrl
          this.moduleResults = response.data.results || []
          this.totalModules = response.data.totalParts || 0
          this.completedModules = this.totalModules
          this.progressStatus = 'success'
          ElMessage.success(`分布生成完成！共处理 ${response.data.totalParts} 个分片，使用 ${response.data.strategy} 策略`)
        } else {
          throw new Error(response.data.error || '生成失败')
        }
      } catch (error) {
        console.error('生成失败:', error)
        this.progressStatus = 'exception'
        this.overallProgress = 0
        ElMessage.error(error.response?.data?.error || error.message || '分布生成测试用例失败，请稍后重试')
      } finally {
        this.loading = false
      }
    },

    handleFileChange(file) {
      const isLt10M = file.size / 1024 / 1024 < 10
      if (!isLt10M) {
        ElMessage.error('文件大小不能超过 10MB!')
        return false
      }
      
      const allowedTypes = ['.txt', '.md', '.docx', '.doc', '.pdf', '.xlsx', '.xls', '.json', '.xml', '.csv']
      const fileName = file.name.toLowerCase()
      const isValidType = allowedTypes.some(type => fileName.endsWith(type))
      
      if (!isValidType) {
        ElMessage.error('不支持的文件格式！')
        return false
      }
      
      this.uploadedFile = file.raw
      ElMessage.success('文件上传成功！')
    },

    handleFileRemove() {
      this.uploadedFile = null
      this.fileList = []
    },

    getModuleTagType(status) {
      switch (status) {
        case '已完成': return 'success'
        case '进行中': return 'warning'
        case '等待中': return 'info'
        case '失败': return 'danger'
        default: return 'info'
      }
    },

    async downloadResult() {
      if (!this.downloadUrl) return

      try {
        const response = await axios.get(this.downloadUrl, {
          responseType: 'blob'
        })

        const blob = new Blob([response.data], { type: 'application/octet-stream' })
        const url = window.URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = url
        link.download = `distributed-test-cases-${new Date().getTime()}.xmind`
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        window.URL.revokeObjectURL(url)

        ElMessage.success('文件下载成功！')
      } catch (error) {
        console.error('下载失败:', error)
        ElMessage.error('文件下载失败')
      }
    },

    async copyToClipboard() {
      try {
        if (navigator.clipboard && window.isSecureContext) {
          await navigator.clipboard.writeText(this.result)
          ElMessage.success('内容已复制到剪贴板！')
        } else {
          this.fallbackCopyToClipboard(this.result)
        }
      } catch (error) {
        console.error('复制失败:', error)
        try {
          this.fallbackCopyToClipboard(this.result)
        } catch (fallbackError) {
          console.error('降级复制也失败:', fallbackError)
          ElMessage.error('复制失败，请手动选择文本复制')
        }
      }
    },

    fallbackCopyToClipboard(text) {
      const textArea = document.createElement('textarea')
      textArea.value = text
      textArea.style.position = 'fixed'
      textArea.style.left = '-999999px'
      textArea.style.top = '-999999px'
      document.body.appendChild(textArea)
      textArea.focus()
      textArea.select()
      
      try {
        const successful = document.execCommand('copy')
        if (successful) {
          ElMessage.success('内容已复制到剪贴板！')
        } else {
          throw new Error('execCommand failed')
        }
      } finally {
        document.body.removeChild(textArea)
      }
    }
  }
}
</script>

